"use strict";

module.exports = function(app){

	require('./styles.css');
	
	app.controller( 'usersCtrl', require('./usersCtrl') );

    app.config(function($stateProvider){

        $stateProvider.state('users', {
            url: '/users',
            template: require('./users.html'),
            controller: 'usersCtrl as vm'
        });

    });

};


